            animation-name: linkbox;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes linkbox {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes linkbox {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.a:hover {
    color: white;
    background-color: black;
    transform: scale(1.1);
    border-radius: 10px;
}

